@media (max-width: 768px) {
}

.main {
  position: fixed;
  top: 0;
  display: flex;
  flex-direction: row;
  height: 4rem;
  background-color: whitesmoke;
  border: none;
  width: 100%;
}

.header {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 1rem;
}

.body {
  padding: 0.3rem;
  display: flex;
  align-items: center;
  flex-grow: 2;
}

.body input {
  border: transparent;
  background-color: white;
  border-radius: 50rem;
  font-size: 1rem;
  padding-left: 2.5rem;
  width: 100%;
  height: 2rem;
}

.footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-grow: 1;
  justify-content: flex-end;
}

.footer .login_button {
  border-radius: 5rem;
  border: none;
  width: 4rem;
  height: 2rem;
  padding: 0.3rem 0;
  margin-right: 0.5rem;
  font-weight: bold;
}
.list {
  position: fixed;
  top: 4rem;
  display: flex;
  flex-direction: column;
  right: 0;
  height: auto;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.list button {
  font-size: 1rem;
  padding: 1rem;
  border: none;
  background-color: white;
}

.body .icon_search {
  position: absolute;
  font-size: 1.5rem;
  margin-left: 0.5rem;
}

.icon_pencil {
  padding: 0.5rem;
  color: black;
  font-size: 2rem;
  border-radius: 100%;
}
.icon_bell {
  color: black;
  padding: 0.5rem;
  font-size: 1.5rem;
  border-radius: 100%;
  background-color: rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0);
}
.icon_arrow {
  color: black;
  padding: 0.5rem;
  font-size: 2rem;
  border-radius: 100%;
  background-color: rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0);
}
