@media (max-width: 768px) {
    .post {
        max-width: 100%; /* 창의 가로 넓이가 768px 이하일 때 최대 넓이를 100%로 변경 */
        margin: 1rem 1rem;
    }
}

@media (hover: hover) {
    .post:hover {
        background-color: lightgrey;
    }

    .footer button:hover {
        background-color: darkgrey;
    }
  }

.main {
    margin-top: 6rem;
}

.post {
    display: flex;
    flex-direction: column;
    padding: 2rem; /* 상하좌우 여백을 조절 */
    border-radius: 2rem;
    margin: 2rem auto; /* auto를 사용하여 좌우 여백을 자동으로 나눔 */
    max-width: 30rem; /* 필요에 따라 최대 넓이를 지정할 수 있음 */
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* 그림자 효과 추가 */
    color: black;
}

.header {
    display: flex;
    flex-direction: row; /* 가로 방향으로 정렬 */
    align-items: center; /* 왼쪽으로 정렬 */
}

.header img {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 100%;
    margin-right: 0.5rem;
 }

.header .name {
    font-size: 0.6rem;
    margin: 0;
}

.body {
    display: flex;
    flex-direction: column;
}

.body img {
    width: auto; /* 이미지의 부모 컨테이너에 대해 100%의 너비를 가지도록 설정 */
    height: 25rem; /* 자동으로 높이를 조정하여 원래 비율을 유지 */
    object-fit: contain;
    max-width: 100%; /* 부모 컨테이너를 넘어가지 않도록 최대 너비 설정 */
    float: left;
    margin-right: 1rem;
    border-radius: 3rem;
}

.body p {
    white-space: pre-line;
}


.footer {  
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-top: 1rem;
}

.footer button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-radius: 5rem;
    border: none;
    font-size: 1rem;
    margin: 0.3rem;
    width: 4rem;
    height: 2rem;
} 

.footer button:hover {
    background-color: darkgrey;
}
