.main {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  
  .form{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 2rem; /* 상하좌우 여백을 조절 */
    border-radius: 2rem;
    margin: 2rem auto; /* auto를 사용하여 좌우 여백을 자동으로 나눔 */
    width: 20rem;
    height: 30rem; /* 필요에 따라 최대 넓이를 지정할 수 있음 */
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* 그림자 효과 추가 */
    color: black;
  }

  .header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .header button {
    background-color: transparent;
    border: none;
    border-radius: 100%;
    padding: 0.5rem;
    font-size: 1.5rem;
  }
  
  .header button:hover {
    background-color: lightgray;
  }

  .body input {
    margin: 0.5rem;
    width: 15rem;
    padding: 0.5rem;
    font-size: 1.2rem;
    border-radius: 10rem;
  }

  .body {

  }

  .footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .footer button {
    background-color: transparent;
    border: none;
    border-radius: 100%;
    padding: 0.5rem;
    font-size: 1.5rem;
  }

  .footer button:hover {
    background-color: lightgray;
  }
  
  .modal-close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 20px;
    cursor: pointer;
    border: none;
    background: transparent;
  }
  