.main {
    display: flex;
    flex-direction: column;
    padding: 2rem;
    border-radius: 2rem;
    margin: 5rem auto;
    max-width: 30rem;
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.header {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.photo_icon {
    font-size: 2rem;
    padding: 0.3rem;
    border-radius: 1rem;
}

.photo_icon:hover {
    background-color: lightgrey;
}

.photo_icon input[type="file"] {
    position: absolute;
    width: 0;
    height: 0;
    padding: 0;
    overflow: hidden;
    border: 0;
}

.body {
    display: flex;
    flex-direction: column;
}

.title {
    margin: 0.5rem 0;
    width: calc(100% - 2.2rem);
    height: 1rem;
	padding: 1rem;
    border: none;
	border-radius: 1rem;
    background-color: whitesmoke;
}

.content {
    margin: 0.5rem 0;
    width: calc(100% - 2.2rem);
	height: 10rem;
	padding: 1rem;
	border: none;
	border-radius: 1rem;
    background-color: whitesmoke;
	resize: none;
}
.footer {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-top: 1rem;
}

.footer button {
    border-radius: 5rem;
    border: none;
    height: 2rem;
    width: 4rem;
    font-size: 1.5rem;
}